import {useState} from 'react';
function Form(){

    const [name,setname] = useState('');
    const [email,setemail] = useState('');
    const [pass,setpass] = useState('');
    const [course,setcourse] = useState("Web Dev");

    function handleData(event){
        event.preventDefault();
        localStorage.name = name;
        localStorage.email = email;
        localStorage.pass = pass;
        localStorage.course = course;
    }

    return(
        <>
        <form onSubmit={handleData}>
        <label>Name:</label>
        <input style={{color:"pink"}} value={name} onChange={(e)=>setname(e.target.value)} type="text"/><br/><br/>
        <label>Email:</label>
        <input  style={{color:"pink"}}value={email} onChange={(e)=>setemail(e.target.value)} type="email"/><br/><br/>
        <label>Password:</label>
        <input style={{color:"pink"}} value={pass} onChange={(e)=>setpass(e.target.value)} type="password"/><br/><br/>
        <label>Emirate:</label>
        <select style={{color:"pink"}} value={course} onChange={(e)=>setcourse(e.target.value)}>
            <option value="Select">Select</option>
            <option value="Abu Dhabi">Abu Dhabi</option>
            <option value="Dubai">Dubai</option>
            <option value="Sharjah">Sharjah</option>
            <option value="Ajman">Ajman</option>
            <option value="Umm Al Quwain">Umm Al Quwain</option>
            <option value="Ras Al Khaimah">Ras AL Khaimah</option>
            <option value="Fujairah">Fujairah</option>
        </select><br/><br/>
        <input style={{color:"pink"}} type="submit" value="submit form"/>
        </form>
        </>

    )
}
export default Form;