import React, { Component } from 'react';

import {Link} from 'react-router-dom';
import Anim from './Anim'
import Axios from'./Axios'
import './Home.css'


class Home extends Component{
    constructor(){
        super();
        this.state = {
            name: localStorage.name,
            email: localStorage.email,
            pass: localStorage.pass,
            course: localStorage.course,
            flag: false
        }
    }

    componentDidMount(){
        setTimeout(()=>{
            this.setState({
                flag: true
            })
        },3000)
    }
    render(){
        return(
            <div className='Home'>
            <nav>
                <Link className='link' to="/">Home</Link>
                <Link className='link' to="/about">About</Link>
                <Link className='link' to="/contact">Contact</Link>
                <Link className='link' to="/kitty">Kitty!</Link>
                <Link className='link' to="/Cat">Cats!</Link>
            </nav>
             {this.state.flag?<></>:<Anim/>}
             {this.state.flag?  <h1 class="aaa">Adopt a Kitty!</h1>:<></>}
             {this.state.flag? <div class="aa">

<h1 >Name: {this.state.name}</h1>
<h1>Email: {this.state.email}</h1>
<h1>Password: {this.state.pass}</h1>
<h1>Emirate: {this.state.country}</h1>
</div>
  :<></>}
          
           
           
            
            <h1 class="a">Let's Try and give these cats a happy home!</h1>
<Axios/>
            
            </div>
        )
    }
}
export default Home;